import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, tap } from 'rxjs';
import { environment } from 'src/environments/environment';
import { CommonService } from '../common/common.service';

@Injectable({
  providedIn: 'root'
})
export class BookingService {
  repairTypes = [];
  /**
   * constructor
   * @param http
   */
  constructor(private http: HttpClient, private commonSerice: CommonService) {
  }

  /**
 * get booking types
 * @returns Observable
 */
  public getBookingRepairTypes(query): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/setting/booking-repair-type${query}`;
    return this.http.get(url);
  }

  /**
 * get booking types
 * @returns Observable
 */
  public getBookingRepairTypesByOrg(query=''): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/setting/booking-repair-type?${query}`;
    return this.http.get(url);
  } 

  /**
   * This is old api replace this with getBookingRepairTypes
   * get booking types
   * @returns Observable
   */
  public getBookingTypes(): Observable<any> {
    const url = `${environment.bookingMgmtUrl}/api/lookup?type=BookingType`;
    return this.http.get(url);
  }

  /**
   * This is old api replace this with getBookingRepairTypes
   * get repair types
   * @returns Observable
   */
  public getRepairTypes(): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/lookup?type=RepairServiceType`;
    return this.http.get(url);
  }

  /**
   * get repair types
   * @returns Observable
   */
  public getRepairStatuses(): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/lookup?type=repairstatus`;
    return this.http.get(url);
  }

  /**
   * get booking detail
   * @returns Observable
   */
  public getBookingByGuid(guid: string): Observable<any> {
    const url = `${environment.bookingMgmtUrl}/api/booking/${guid}`;
    return this.http.get(url);
  }

  /**
   * get repair detail
  */
  public getRepairDetailByGuid(guid: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repair/${guid}`;
    // Set Domain ID when api is called
    return this.http.get(url).pipe(tap((el)=> {
      this.commonSerice.domainId.next(el?.data?.domainId);
    }));
  }

  /**
   * add repair booking
   */
  addRepairBookings(bookingData: object): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repair`;
    return this.http.post(url, bookingData);
  }

  /**
   * Update repair booking
   * @param vehicleUpdateCaseData
   * @returns
   */
  updateRepairBooking(updateData: any, repairId: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repair`;
    return this.http.put(url, updateData);
  }

  /**
   * Update repair status
   * @param statusId
   * @param repairid
   * @returns
   */
  updateRepairStatus(status: string, repairId: string, reason: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repair/${repairId}/status`;
    return this.http.put(url, {
      status,
      reason
    });
  }

  /**
   * get repair tasks
   */
  getRepairTask(repairId: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repairstages/${repairId}/repairjobs`;
    return this.http.get(url);
  }

  /**
   * update repair task nodes
   */
  updateRepairTaskNotes(repairId: string, notes: string, repairTaskGuid: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repairstages/repairjob/${repairId}/tasknote`;
    const body = {
      repairTaskGuid,
      notes
    }
    return this.http.put(url, body);
  }

  /**
   * update repair timer status
   */
  updateRepairTimerStatus(repairId: string, status: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repairstages/repairjob/${repairId}/timer/${status}`;
    return this.http.post(url, null);
  }

  /**
   * update repair timer status
   */
  updateRepairTaskStatus(repairTaskGuid: string, repairId: string, status: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/repairstages/repairjob/${repairId}/taskstatus`;
    const body = [ {
      'repairTaskGuid': repairTaskGuid,
      status
    } ]
    return this.http.put(url, body);
  }

  /**
   * Get employeess by competencies
   * @param competencies
   * @param startDate
   * @param endData
   */
  getEmployeesByCompetencies(competenciesIds: number[] = null, startDate: string, endDate: string): Observable<any> {
    const url = `${environment.bookingMgmtUrl}/api/employee/get-by-criteria`;
    const data = competenciesIds == null ? {
      startDate,
      endDate
    } : {
      competenciesIds,
      startDate,
      endDate
    }
    return this.http.post(url, data);
  }

  /**
   * update repair status externally
   * @param status
   * @param repairId
   * @param reason
   * @param token
   * @returns
   */
  updateRepairStatusExternally(status: string, repairId: string, reason: string, token: string): Observable<any> {
    const url = `${environment.caseMgmtUrl}/api/external/${repairId}/status`;
    const headers = new HttpHeaders().append('token', decodeURIComponent(token))
    return this.http.put(url, {
      status,
      reason
    }, {
      headers
    });
  }

}
