import localeAr from '@angular/common/locales/ar-AE';
import localeIn from '@angular/common/locales/en-IN';
import localeEn from '@angular/common/locales/en';
import localeGb from '@angular/common/locales/en-GB';
import localeSr from '@angular/common/locales/ar-SA';
import localeQa from '@angular/common/locales/ar-QA';

import { NumberSuffixPipe } from './helper/pipe/number-suffix/number-suffix.pipe';
import { MatSidenavModule } from '@angular/material/sidenav';
import { NO_ERRORS_SCHEMA } from '@angular/compiler';

registerLocaleData(localeIn);
registerLocaleData(localeAr);
registerLocaleData(localeEn);
registerLocaleData(localeGb);
registerLocaleData(localeSr);
registerLocaleData(localeQa);
@Injectable()
export class MyHammerConfig extends HammerGestureConfig {
  // override overrides = <any>{
  //   'swipe': {
  //     // override default settings
  //     velocity: 0.4,
  //     threshold: 20,
  //     direction: Hammer.DIRECTION_ALL,
  //     enabled: true
  //   }
  // };
}

@NgModule({
  declarations: [ AppComponent,
    AlertDialogComponent,
    ConfirmDialogComponent,
    ToastComponent,
    NotificationComponent,
    GtMotiveEstimationDialogComponent
  ],
  providers: [
    { provide: UrlSerializer, useClass: CustomUrlSerializer },
    interceptorProviders,
    [ { provide: DEFAULT_AI_TIMEOUT, useValue: environment.ai.recursionTimeout },
      { provide: RECURSION_MAX_RETRIES, useValue: environment.ai.maxRetryCount },
      { provide: ENABLE_AI_RECURSION, useValue: environment.ai.enableRecursion } ],
    MSALProviders,
    {
      provide: HAMMER_GESTURE_CONFIG,
      useClass: MyHammerConfig
    },
    DatePipe,
    NumberSuffixPipe,
    CurrencyPipe
  ],
  bootstrap: [ AppComponent ],
  imports: [
    HammerModule,
    BrowserModule,
    BrowserAnimationsModule,
    LayoutModule,
    RouterModule.forRoot(routes, { 
      preloadingStrategy: PreloadAllModules 
    }),
    IconsModule,
    HttpClientModule,
    MatProgressSpinnerModule,
    MatSnackBarModule,
    MatDialogModule,
    MatIconModule,
    MatMenuModule,
    MatExpansionModule,
    MatChipsModule,
    NgxTranslateModule
  ]
})
export class AppModule { }

export function appConfigFactory(
  provider: CoreService,
  msalService: MsalService,
  msalBroadcastService: MsalBroadcastService,
  translate: TranslateService
) {
  return () => {
    new Promise((resolve, reject) => {
      const subs =
        msalBroadcastService.msalSubject$
          .subscribe((result: any) => {
            if (result.eventType === 'msal:handleRedirectEnd') {
              // console.log('2');
              // provider.getConfiguration()
              //   .subscribe({
              //     next: (res: any) => {
              //       localStorage.setItem('languageFile', res.data.files[0].fileUrl);
              //       localStorage.setItem('configFile', res.data.configurationFileURL);


              //     }
              //   })

              this.commonService.showLoading();
              provider.getTranslation().subscribe((x) => {
                this.commonService.hideLoading();
                translate.setTranslation('en', x);
                translate.use('en');

                subs.unsubscribe();
                resolve(true);
              })

            }
          });
    });
  }
}